body {
    margin: 60px auto;
    width: 50%;
    font-family:'Helvetica', 'Arial', 'SF Pro Text';
    color: #555;
}

h1 {
    font-size: 3em;
    color: #333;
    margin-bottom: 0px;
}

h2 {
    font-size: 1.5em;
    font-weight: normal;
    color: #555;
}

h3 {
    font-size: 1.25em;
    font-weight: normal;
    color: #555;
}

h4 {
    font-size: 1em;
    font-weight: normal;
    color: #666;
}

h5 {
    margin-top: 0px;
    font-size: 0.9em;
    font-weight: normal;
    color: #888;
}

p { color: #777; }

nav {
    padding-bottom: 20px;
    border-bottom: 1px solid #d5d5d5;
    margin-bottom: 75px;
}

footer {
    border-top: 1px solid #d5d5d5;
    margin-top: 50px;
    padding-top: 10px;
    font-size: 0.9em;
    font-weight: 600;
}

nav a, footer a {
    display: inline;
    margin-right: 20px;
    text-decoration: none;
    vertical-align: middle;
    color: #999;
}

nav a:hover, footer a:hover {
    text-decoration: none;
    color: #666;
}

ul.posts {
    margin: 20px auto 40px;
    font-size: 1.5em;
}

ul.posts li {
    list-style: none;
}

.icon {
  width: 1em;
  height: 1em;
  vertical-align: middle;
  color: #999;
}

ul.blog-post-list li {
    list-style: none;
}

ul.blog-post-list a {
    text-decoration: none;
}

ul.blog-post-list a:hover {
    text-decoration: none;
}

.post-link-title {
    font-size: 1.0em;
    color: #555;
    font-weight: 600;
}

.post-link-date {
    margin-left: 20px;
    color: #bbb;
    float: right;
}

.mjx-math {
  font-size: 0.93em;
}
